<template>
    <div class="container">
        <oldnav v-bind:isLogged="isLogged" v-bind:user="user"></oldnav>
        <br>
        <h1 style="text-align:center"></h1>
        <hr>
        <h3 style="color:darkred;text-align: center;">: <strong>{{user.region}}</strong></h3>
        
        <b-table hover :items="results" :fields="fields"></b-table>
    </div>
</template>

<script>
    import axios from 'axios';
    import oldnav from './oldnav.vue';

    export default{
        name:'results',
        components:{oldnav},
        data(){
            return{
                fields:{
                    uniNameCN:{
                        label: '',
                        sortable: true,
                    },
                    offTimeDiff:{
                        label: '()',
                        sortable: true,
                    }
                },
                results:'',
                region: '',
                number:3,
            }
        },
        mounted(){
            this.getResults();
        },
        computed:{
            user: function(){ return JSON.parse(localStorage['user'])},
            isLogged : function(){ return this.$store.getters.isLoggedIn},
        },
        methods:{
            getResults:function(){
                axios
                    .get('api/time/official/get',{
                        headers:{
                            Authorization: "Bearer " + localStorage.getItem('token')
                        }
                    })
                    .then(resp=>{
                        
                        if (resp.data.region=='Singapore') {this.number = 2;}
                            if (resp.data.region=='Malaysia') {this.number = 4;}
                            if (resp.data.region=='Hong Kong') {this.number = 1;}
                            if (resp.data.region=='Taiwan') {this.number = 1;}
                            if (resp.data.region=='Macau') {this.number = 1;}
                            if (resp.data.region=='Australia') {this.number = 4;}
                            if (resp.data.region=='Admin') {this.number = 5;}
                            if (resp.data.region=='China') {this.number = 8;}
                            if (resp.data.region=='Others') {this.number = 1;}
                        console.log(this.number);
                        console.log(resp.data.region);

                        this.results = resp.data.data.slice(0,this.number);
                        console.log(this.results);
                        for(var i = 0; i < this.results.length; i++){
                            if(this.results[i].offTimeDiff == 1000000000000000000){
                                this.results[i].offTimeDiff = "";
                            }else if(this.results[i].offTimeDiff == 0){
                                this.results[i].offTimeDiff = ""
                            }else{
                                this.results[i].offTimeDiff = this.results[i].offTimeDiff/1000000 //show time in second from micro
                            }
                        }
                        
                        
                        switch(resp.data.region){
                            case 'Singapore': this.region = ""; break;
                            case 'Malaysia': this.region = ""; break;
                            case 'Hong Kong': this.region = ""; break;
                            case 'Macau': this.region = ""; break;
                            case 'China': this.region = ""; break;
                            case 'Australia': this.region = ""; break;
                            case 'Taiwan': this.region = ""; break;
                            case 'Others': this.region=""; break;
                            default: break;
                        }
                    })
            }
        }
    }
    
</script>


